import {
  CHANGE_COMPANY_PLAN_FAILURE,
  CHANGE_COMPANY_PLAN_SUCCESS,
  CHANGE_COMPANY_PLAN_START,
} from "../types";
const initialState = {
  loaders: {
    subscribingToPlan: null,
  },
  errors: {
    subscribingToPlan: false,
  },
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    //   company plan subscription start
    case CHANGE_COMPANY_PLAN_START: {
      return {
        ...state,
        loaders: { ...initialState.loaders, subscribingToPlan: true },
      };
    }
    case CHANGE_COMPANY_PLAN_SUCCESS: {
      return {
        ...state,
        loaders: { ...initialState.loaders, subscribingToPlan: false },
      };
    }
    case CHANGE_COMPANY_PLAN_FAILURE: {
      return {
        ...state,
        loaders: { ...initialState.loaders, subscribingToPlan: undefined },
      };
    }
    //   company plan subscription end

    default:
      return state;
  }
};
